section{
    height: calc(100vh - 10vh);
    width: 100%;
        
}
#home{
    display: flex;
    justify-content: center;
    align-items: center;
    gap:50px;
}

#home .social{
    display: flex;
    flex-direction: column;
    color: #fff;
 gap: 10px;
margin-bottom: 300px;
margin-right: 30px;
margin-left: 20px;
}

#home .left{
    width: 550px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap:30px;

}
#home .left h1{
    font-size: 5em;
    text-transform: capitalize;
}
#home .left h1 span{
    color:var(--divhover);
    text-transform: uppercase;
}
#home .left h2{
    text-transform: uppercase;
}
#home .left .buttons{
width: 300px;
}
#home .left .buttons a{
    background-color: var(--divcolor);
    padding:15px 30px;
    color:var(--textcolor);
    opacity: .8;
    transition:var(--transition);
    border:2px solid transparent;
    text-transform: uppercase;
}
#home .left .buttons a:hover{
    background-color: var(--divhover);
    border:2px solid var(--textcolor);
    color:var(--textcolor);
}
#home .right{
    width: 700px;
    height: 800px;
    display: flex;
    /* justify-content: center; */
    align-items: center;
}
#home .right {
    background-image: url("../../assest/me.png");
    background-size: cover;
    /* height: 500px; */
    display: flex;
    /* justify-content: center; */
    align-items: center;
}