.skills {
  background-color: var(--bgcolor);
  height: 100vh;
  padding: 0 100px;
}

.maintext h1 {
  margin-top: 10px;
  
  margin-bottom: 30px;
  font-size: 30px;
  font-family: sans-serif;
  font-weight: 600;
  /* background-color: #fff;   */
  text-align: center;
}

.skills-main {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 30px;
  grid-column-gap: 50px;
  /* padding: 20px; */
}

.skills-bar {
  margin-bottom: 2.3rem;
}

.skills-main h3 {
  margin-bottom: 3rem;
  /* font-size: var(--normal-font); */
  text-align: center;
  font-size: 24px;
  font-weight: 600;
}

.skills-bar .info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}

.skills-bar .bar {
  width: 100%;
  height: 15px;

  
  background-color: var(--divcolor);
  border-radius: 25px;
  margin-top: 10px;
  position: relative;
}

.skills-bar span {
  width: 50%;
  height: 100%;
  position: absolute;
  left: 0;
  background-color:var(--divhover);

  border-radius: 25px;
  animation: html 5s linear;
}

.info p {
  font-size: 20px;
  font-family: sans-serif;
  font-weight: 500;
}

.skills-bar .html {
  width: 72%;
  color: blue;
}
.skills-bar .css {
  width: 85%;
  color: blue;
}
.skills-bar .javascript {
  width: 75%;
  color: blue;
}
.skills-bar .react {
  width: 89%;
  color: blue;
}
.skills-bar .MongoDB {
  width: 80%;
  color: blue;
}

@keyframes html {
  0% {
    width: 0%;
  }
  100% {
    width: 72%;
  }
}

@keyframes css {
  0% {
    width: 0%;
  }
  100% {
    width: 85%;
  }
}

@keyframes javascript {
  0% {
    width: 0%;
  }
  100% {
    width: 75%;
  }
}

@keyframes react {
  0% {
    width: 0%;
  }
  100% {
    width: 89%;
  }
}

