* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section {
  height: 100vh;
  background-color: var(--bgcolor);
}

.about-main {
  display: flex;
  justify-content: center;
  gap: 100px;
}

.main-text {
  text-align: center;
}

.about_image {
  height: 300px;
  width: 300px;
  border-radius: 20px;
  transition: 0.5s;
  overflow: hidden;
  margin: 70px 70px 100px 130px;
}

.about_image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(2px);
  transition: 0.5s;
}
.about_image img:hover {
  transform: scale(1.2) rotate(10deg);
  filter: blur(0px);
}

.about_cards {
  width: 500px;
  display: flex;

  gap: 20px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.about_cards p {
  text-align: justify;
  line-height: 1.5;
}
.card {
  display: flex;
  width: 500px;
  gap: 20px;
}
.about_card {
  width: 200px;
  background-color: var(--divcolor);
  border: 1px solid black;
  border-radius: 1rem;

  padding: 2.5rem;
  text-align: center;
  transition: var(--transition);
}

.about_card:hover {
  background-color: var(--divhover);
  border:2px solid var(--textcolor);
  cursor: default;
  color: rgb(230, 252, 173);
}

