#portfolio {
  height: 120vh;
  width: 100vw;
  background-color: var(--bgcolor);
}

.heading {
  text-align: center;
  padding: 15px;
}

.portfolio-contaniar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 50px;
  gap: 50px;
}

.porfolio-item {
  border-radius: 2rem;
  background-color: var(--divcolor);
  transition: var(--transition);
  border:2px solid transparent;
}

.porfolio-item:hover {
  background-color: var(--divhover);
  border:2px solid var(--textcolor);
  transition: 0.5s;
}

.porfolio-item-image {
  border-radius: 1.5rem;
  overflow: hidden;
  padding: 50px;
  margin-top: 20px;
}
#sec{
  padding: 60px;
}
.porfolio-item-image {
  height: 250px;
  width: 350px;
  position: relative;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}
.porfolio-item-image img {
  position: absolute;
  height: 100%;
  border-radius: 1.5rem;
}

.porfolio-item h3 {
  margin: 5px 0 0.5rem 10px;
  text-transform: capitalize;
  text-align: center;
}

.link {
  font-size: 18px;
  text-decoration: none;
  font-weight: 300;
  text-align: center;
  color:var(--textcolor);
}
