nav {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;

  background-color: var(--bgcolor);
}
nav a {
  text-decoration: none;
  color: var(--textcolor);
  font-size: 25px;
  font-weight: bold;
  font-family: Georgia, "Times New Roman", Times, serifcxzZXcvgbh;
}

nav ul {
  display: flex;
  gap: 20px;
}

nav ul li {
  list-style: none;
}

.li {
  list-style: none;
  font-size: 17px;
  font-weight: 500;
  cursor: pointer;
  color: var(--textcolor);
  transition: var(--transition);
  list-style: none;
}
.menu {
  font-size: 20px;
  color: var(--textcolor);
  display: none;
}
.hover li:hover {
  transform: scale(1.1, 1.1);
  color: var(--textcolor);
  font-weight: bold;
}
.hover:hover > li:not(:hover) {
  filter: blur(2px);
  transform: scale(0.9, 0.9);
}
.btn {
  margin-right: -80px;
  font-size: 15px;
  background-color: var(--divcolor);
  padding: 10px 15px;
  transition: 1s;
  cursor: pointer;
  color: var(--textcolor);
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  transition: var(--transition);
}
.btn:hover {
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
  border-top-right-radius: 0;
  border-bottom-left-radius: 0;
  background-color: var(--divhover);
}

@media screen and (max-width: 400px) {
  .portfolioLogo {
    margin-left: -30px;
  }
   .hover {
    display: none;
  }
  .btn {
    display: none;
  }

  .menu {
    display: block;
    margin-right: -40px;
  }
}

@media screen and (min-width: 401px) and (max-width: 720px) {
  .portfolioLogo{
margin-left: -120px;

  }
  
  
  .hover {
    display: none;
  }
  .btn {
    display: none;
  }
  .menu{
    display: block;
margin-right: -120px;

  }
}

