#contact {
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--bgcolor);
}


.container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  gap: 12%;
}

.maintext{
  background-color: var(--bgcolor);
}

.maintext h3{
font-size: 35px;
  text-align: center;
  margin-top: 60px;
  font-weight: 900;
  font-family: sans-serif;
  
}

.contact_options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.contact_option {
  background-color: var(--divcolor);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  justify-content: center;
  border: 1px solid;
  transition: var(--transition);
  
}

.contact_option:hover {
  background-color: var(--divhover);
  border-color: var(--color-primary-variant);
  border:2px solid var(--textcolor);
  transition: 0.5s;
}

.contact_option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.contact_option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

/* *************** form*************** */
form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  border:2px solid var(--divcolor);
  background-color: var(--divcolor);
  color: var(--textcolor);
 
  resize: none;
}


#btn{
  width: 462px;
  border-style: none;
  font-size: 20px;
  background-color: var(--divcolor);
}

/* ======================= media queries (medium devices)========================= */
@media screen and (max-width: 1024px) {
  .container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}
@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
}
