.fixedMenu{
background: rgb(0, 0, 0,0.3);
width: max-content;
display: block;
position: fixed;
z-index: 2;
transform: translateX(-50%);
bottom: 2rem;
display:flex; 
gap: 8 px;
border-radius: 3rem;
backdrop-filter: blur(15px);
margin-left: 700px;
}

.floaticon{
    color: #fff;
    font-size: 20px;
}

.fixedMenu a{
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--color--light);
    font-size: 1.1rem;
    transition: var(--transition);
}

nav a:hover {
    background :rgb(0, 0, 0,0.3)
}

nav a.active{
    background:var(--color-bg);
    color:var(--color--white);
}